import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    value: {},
};
const mutations = {
    setInfo(s, stats) {
        Vue.set(s, 'info', stats);
    },
    setStatsInfo(s, stats) {
        Vue.set(s, 'value', stats);
    },
    setContractsInfo(s, stats) {
        Vue.set(s, 'contracts', stats);
    },
    setContractsList(s, stats) {
        Vue.set(s, 'contractsList', stats);
    },
    setStorageUsedTest(s, gb) {
        let b =  gb*Math.pow(1000, 3)
        Vue.set(s.value.host_stats, 'storage_cap', b);
    }
};
const getters = {
    statsInfo(s) {
        return s.value;
    },
    contractInfo(s) {
        return s.value;
    }
};
const actions = {
    storageInfo(context) {
        return request({
            endpoint: 'v1/storage/info',
            method: 'GET',
        }).then((stats) => {
            // console.log('storage/info\n', JSON.parse(stats))
            context.commit('setInfo', JSON.parse(stats));
        });
    },
    storageStatsInfo(context) {
        return request({
            endpoint: 'v1/storage/stats/info',
            method: 'GET',
        }).then((stats) => {
            // console.log('storage/stats/info\n', JSON.parse(stats))
            context.commit('setStatsInfo', JSON.parse(stats));
        });
    },
    storageStatsSync() {
        return request({
            endpoint: 'v1/storage/stats/sync',
            method: 'GET',
        })
    },
    storageAnnounce(context, x) {
        let body = {
            'host-storage-max':x
        }
        return request({
            endpoint: 'v1/storage/announce',
            method: 'POST',
            body: body,
            params: {
                'host-storage-max': x
            }
        }).then(() => {
            // console.log('announcing ->', body, x*Math.pow(1000, 3))
            return context.dispatch('storageStatsInfo')
        })
    },
    storageContractsInfo(context) {
        return request({
            endpoint: 'v1/storage/contracts/stat',
            method: 'GET',
            params: {
                role: 'host'
            }
        }).then((stats) => {
            // console.log('storage/contract/stat\n', JSON.parse(stats))
            context.commit('setContractsInfo', JSON.parse(stats));
        });
    },
    storageContractsSync() {
        return request({
            endpoint: 'v1/storage/contracts/sync',
            method: 'GET',
            params: {
                role: 'host'
            }
        })
    },
    storageContractsList(context) {
        return request({
            endpoint: 'v1/storage/contracts/list',
            method: 'GET',
            params: {
                role: 'host',
                order:"escrow_time,asc",
                status:"active",
                size:20,
            }
        }).then((stats) => {
            // console.log('storage/contracts/list', JSON.parse(stats))
            context.commit('setContractsList', JSON.parse(stats));
        });
    }
};

export default {
    state,
    getters,
    mutations,
    actions,
};